@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,300;0,400;0,500;0,700;1,600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{box-sizing:0;margin:0;font-family:"Montserrat", sans-serif}body{background-color:black}svg{height:100vh;width:100vw}@-webkit-keyframes rr{to{transform:rotate(var(360))}}@keyframes rr{to{transform:rotate(var(360))}}.grid-container{display:grid;margin-top:16px;margin-left:32px;grid-template-columns:repeat(8, 1fr);grid-template-rows:repeat(4, 1fr);grid-column-gap:1em;-webkit-column-gap:1em;column-gap:1em;grid-row-gap:1em;row-gap:1em}.swiss-circle-container{position:relative;margin-left:-2em;margin-top:-1em;width:200px;height:200px;grid-column:1/1;grid-row:1/1;z-index:-1}.swiss-circle-container #circ-1{position:relative;transform:translate(18%, 50%);stroke-width:10px}.swiss-circle-container #circ-2{position:relative;transform:translate(18%, 50%);stroke-width:20px}.swiss-circle-container #circ-3{position:relative;transform:translate(18%, 50%);stroke-width:40px}.swiss-circle-container #circ-4{position:relative;transform:translate(18%, 50%);stroke-width:80px}.swiss-circle-container #circ-5{position:relative;transform:translate(18%, 50%);stroke-width:160px}.swiss-circle-container #circ-6{position:relative;transform:translate(18%, 50%);stroke-width:320px}#title-container{grid-column:1 / span 4;grid-row:3/4}#name{color:white;font-size:3.8em;font-weight:600}#sub-title{color:white;font-weight:400;font-size:2em;position:relative;left:35%}

