@import url("https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,300;0,400;0,500;0,700;1,600&display=swap");

* {
  box-sizing: 0;
  margin: 0;
  font-family: "Montserrat", sans-serif;
}
body {
  background-color: black;
}

@mixin center-element {
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

svg {
  height: 100vh;
  width: 100vw;
}

@keyframes rr {
  to {
    transform: rotate(var(360));
  }
}

.grid-container {
  display: grid;
  margin-top: 16px;
  margin-left: 32px;
  grid-template-columns: repeat(8, 1fr);
  grid-template-rows: repeat(4, 1fr);
  column-gap: 1em;
  row-gap: 1em;
}

.swiss-circle-container {
  position: relative;
  margin-left: -2em;
  margin-top: -1em;
  width: 200px;
  height: 200px;
  // @include center-element;
  grid-column: 1/1;
  grid-row: 1/1;
  z-index: -1;
  #circ-1 {
    position: relative;
    transform: translate(18%, 50%);
    stroke-width: 10px;
  }
  #circ-2 {
    position: relative;
    transform: translate(18%, 50%);
    stroke-width: 20px;
  }
  #circ-3 {
    position: relative;
    transform: translate(18%, 50%);
    stroke-width: 40px;
  }
  #circ-4 {
    position: relative;
    transform: translate(18%, 50%);
    stroke-width: 80px;
  }
  #circ-5 {
    position: relative;
    transform: translate(18%, 50%);
    stroke-width: 160px;
  }
  #circ-6 {
    position: relative;
    transform: translate(18%, 50%);
    stroke-width: 320px;
  }
}

#title-container {
  grid-column: 1 / span 4;
  grid-row: 3/4;
  // align-self: end;
  // background-color: blue;
}

#name {
  color: white;
  font-size: 3.8em;
  font-weight: 600;
}

#sub-title {
  color: white;
  font-weight: 400;
  font-size: 2em;
  position: relative;
  left: 35%;
}
